.new-expense {
    background-image: linear-gradient(to right, rgb(161, 166, 230) , rgb(6, 95, 33));
padding: 1rem;
margin: 2rem auto;
width: 44rem;
max-width: 95%;
border-radius: 12px;
text-align: center;
box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
font: inherit;
cursor: pointer;
padding: 1rem 2rem;
border: 1px solid #40005d;
background-color: #40005d;
color: white;
border-radius: 12px;
margin-right: 7rem;
}

.new-expense button:hover,
.new-expense button:active {
background-color: #510674;
border-color: #510674;
}

.new-expense button.alternative {
color: #220131;
border-color: transparent;
background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
background-color: #ddb3f8;
}